@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300;400;500;600&family=Roboto:wght@300;400;500;700;900&display=swap');


body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  font-family: 'Roboto Slab', serif;
}

.item-button button {
  margin-top: 20px;
  width: 200px;
  background-color: rgb(34, 78, 9);
  color: white;
}

.spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 100;
}

.spinner-border {
  height: 100px;
  width: 100px;
}

.category{
  border: 2px solid gray;
  margin-right: 25px;
  margin-bottom: 10px;
  cursor: pointer;
  padding: 10px;
}
.category img{
  margin-left: 10px;
}

.category-active{
  border: 2px solid green;
  background-color: rgba(128, 128, 128, 0.501);
}


.register {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
}

.register input {
  width: 400px !important;
}

.regsiter-form {
  background-color: rgba(98, 197, 251, 0.463);
  padding: 20px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px,
    rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}

.regsiter-form h1 {
  text-align: center;
  background-color: rgb(2, 2, 75);
  color: white;
  padding: 10px;
}

.regsiter-form h3 {
  color: rgb(4, 4, 113);
  text-align: center;
}


/************* Media Queries ****/
@media only screen and (max-width: 768px) {
  /* For mobile phones: */
  #components-layout-demo-custom-trigger .trigger {
    width: 80%;
  }
  
  #components-layout-demo-custom-trigger .trigger:hover {
    color: #1890ff;
  }
  
  #components-layout-demo-custom-trigger .logo {
    height: 22px;
    margin: 6px;
    background: rgba(255, 255, 255, 0.3);
  }
  
  .site-layout .site-layout-background {
    background: #fff;
    box-shadow: 0 0 3px #ccc;
    border-radius: 5px;
    margin: 10px;
    overflow: auto;
    width: 80%;
  }
  
  .ant-layout-has-sider {
    padding: 5px !important;
  }
  
  .ant-layout.ant-layout-has-sider {
    height: 90vh; 
  }
  
  .ant-layout-sider {
    width: 10%;
  }
  
  .anticon svg {
    height: 20px;
    width: 20px;
    font-size: 20px;
    display: flex;
    align-items: center;
    margin-left: 10px;
  }
  
  .ant-layout-header {
    line-height: 0;
    display: flex;
    align-items: center;
    justify-content:flex-start;
    width: 50%;
  }
  .homeClass{
    display: flex; flex-direction: column; width: 13rem;
  }
  .homeRow{
    display: flex; flex-direction: column;


  }
  
  .contentClass{
    width: 50vh;
    height: 100vh;
  }
  .cart-item{
    margin-right: 4.5rem;
  }
  .register{
    justify-content: start;
    width: 100%;
    margin-top: 5rem;
    margin-right: 2rem;
    background-color: rgb(170, 246, 246);
  }
  .regsiter-form{
    width: 57%; 
    
  }
}